#main {
    background: #ccc;
    background: url('../images/bg1.jpg');
    background-size: cover;
    height: 100vh;
    /* cursor: none; */
    font-family: 'Noto Sans', sans-serif;
    overflow: hidden;
    margin: 0;
    padding: 0;
    background-color: #22374C;
    overflow: hidden;
}

#shadow {
    margin: 0 auto 0 auto;
    font-size: 10vw;
    text-align: center;
    text-transform: uppercase;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

#lantern {
    position: sticky;
    /* background: url('https://res.cloudinary.com/dc4stsmlc/image/upload/v1570612772/Codepen/glowstick_hmpnql.png') no-repeat; */
    background-size: contain;
    width: 150px;
    height: 140px;
}

#lantern:before {
    position: absolute;
    top: -538px;
    left: -517px;
    height: 1200px;
    width: 1200px;
    content: '';
    border-radius: 50%;
    z-index: -4;
    background: -webkit-radial-gradient(rgba(116, 253, 176, 0.8) -1%, rgba(164, 255, 141, 0.49) 16%, rgba(156, 254, 132, 0.42) 21%, transparent 57%);
     /*background: -moz-gradient(rgba(116, 253, 176, 0.8) -1%, rgba(164, 255, 141, 0.49) 16%, rgba(156, 254, 132, 0.42) 21%, transparent 57%);*/
    background: radial-gradient(rgba(116, 253, 176, 0.8) -1%, rgba(164, 255, 141, 0.49) 16%, rgba(156, 254, 132, 0.42) 21%, transparent 57%);

}

#shadow {
    color: #E0E1D8;
